.block-team {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team__inner {
  padding: 110px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 45px;
}
.block-team__inner .block-title {
  max-width: 475px;
  color: rgb(var(--primary-color));
  text-align: center;
}
.block-team__inner .block-title span,
.block-team__inner .block-title strong {
  color: rgb(var(--secondary-color));
}
.block-team .team-members {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team .team-members__wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.block-team .team-members .member {
  min-width: 280px;
  padding: 0 15px;
  flex: 1 0 25%;
  display: flex;
}